/* Fonts */
@font-face {
  font-family: "Kaushan Script";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/KaushanScript-Regular.woff2") format("woff2"), url("../fonts/KaushanScript-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto-Italic.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 300;
  src: url("../fonts/Roboto-LightItalic.woff2") format("woff2"), url("../fonts/Roboto-LightItalic.woff") format("woff");
  font-display: swap;
}
/* Normalize */
body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Container */
.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Page */
.page__body {
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

/* Intro */
.intro {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  background-image: url(../img/intro/intro-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #e89584;
}
@media (max-width: 1025px) {
  .intro {
    height: auto;
  }
}

.intro__main {
  margin-top: 90px;
}
@media (max-width: 1025px) {
  .intro__main {
    margin-top: 15px;
  }
}

/* Header */
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Logo */
.logo__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 30px;
  text-decoration: none;
  color: #fff;
  padding: 34px 0;
}
.logo__link:hover {
  color: #fce38a;
  transition: color 0.3s linear;
}
@media (max-width: 1025px) {
  .logo__link {
    font-size: 24px;
    padding: 15px 0;
  }
}

/* Main-nav */
.main-nav__list {
  display: flex;
  align-items: center;
  min-width: 620px;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1025px) {
  .main-nav__list {
    min-width: 450px;
  }
}

.main-nav__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  line-height: 1.6;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  padding: 40px 0;
  position: relative;
}
@media (max-width: 1025px) {
  .main-nav__link {
    font-size: 12px;
    padding: 15px 0;
  }
}
.main-nav__link--current {
  color: #fce38a;
  cursor: pointer;
}
.main-nav__link--current::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fce38a;
  position: absolute;
  bottom: 33px;
  left: 0;
}
@media (max-width: 1025px) {
  .main-nav__link--current::after {
    bottom: 10px;
    height: 2px;
  }
}
.main-nav__link:not(.main-nav__link--current):hover {
  color: #fce38a;
  transition: color 0.3s linear;
}
.main-nav__link:not(.main-nav__link--current)::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fce38a;
  position: absolute;
  bottom: 33px;
  left: 0;
  transform: scale(0);
}
@media (max-width: 1025px) {
  .main-nav__link:not(.main-nav__link--current)::after {
    bottom: 10px;
    height: 2px;
  }
}
.main-nav__link:not(.main-nav__link--current):hover::after {
  transform: scale(1);
  transition: transform 0.3s linear;
}
.main-nav__link:hover svg path {
  fill: #fce38a;
  transition: fill 0.3s linear;
}

@media (max-width: 1025px) {
  .main-nav__link svg {
    transform: scale(0.8);
  }
}

/* Main */
.main {
  text-align: center;
}

.main__suptitle {
  font-family: "Kaushan Script", sans-serif;
  font-size: 72px;
  color: #fff;
  margin-bottom: 20px;
}
@media (max-width: 1025px) {
  .main__suptitle {
    font-size: 36px;
  }
}

.main__title {
  max-width: 850px;
  margin: 0 auto;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 150px;
  text-transform: uppercase;
  line-height: 0.9;
  color: #fff;
}
.main__title::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #fff;
  margin: 65px auto 45px;
}
@media (max-width: 1025px) {
  .main__title::after {
    margin-top: 45px;
  }
}
@media (max-width: 1025px) {
  .main__title {
    font-size: 80px;
  }
}

.main__button {
  margin-bottom: 35px;
}

/* Button */
.button {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  border: 3px solid #fff;
  padding: 12px 30px;
}
.button:hover {
  background-color: #000;
  transition: background-color 0.3s linear;
}

/* Footer */
.footer {
  margin-top: auto;
}

.footer__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 30px;
}

.footer__item {
  font-size: 16px;
}
@media (max-width: 1025px) {
  .footer__item {
    font-size: 12px;
  }
}

.footer__item b {
  font-weight: 700;
  font-size: 24px;
}
@media (max-width: 1025px) {
  .footer__item b {
    font-size: 16px;
  }
}

.footer__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  padding: 30px 0;
  position: relative;
}
.footer__link::before {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fff;
  position: absolute;
  top: 10px;
  left: 0;
}
.footer__link:not(.footer__link--current):hover {
  transition: color 0.3s linear;
  color: #4a4a4a;
}
.footer__link:not(.footer__link--current)::after {
  content: "";
  display: block;
  width: 70px;
  height: 3px;
  z-index: 2;
  background-color: #4a4a4a;
  position: absolute;
  top: 10px;
  left: 0;
  opacity: 0;
  transition: opacity 0.2s linear;
}
.footer__link:not(.footer__link--current):hover::after {
  opacity: 1;
}

.footer__link--current {
  color: #f38181;
}
.footer__link--current:after {
  content: "";
  display: block;
  width: 70px;
  height: 3px;
  z-index: 2;
  background-color: #f38181;
  position: absolute;
  top: 10px;
  left: 0;
}

/* Section */
.section--design {
  background-image: url("../img/design/design-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-bottom: 580px;
}
@media (max-width: 1025px) {
  .section--design {
    padding-bottom: 400px;
  }
}

.section--team {
  padding-bottom: 40px;
}

.section--happy-clients {
  background-color: #ccc;
  background-image: url("../img/happy-clients/happy-clients-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.section__wrapper {
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
}

.section__suptitle {
  font-family: "Kaushan Script", sans-serif;
  font-size: 24px;
  line-height: 1;
  color: #333;
  margin-top: 95px;
  margin-bottom: 25px;
}

.section__title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 1;
  text-transform: uppercase;
  color: #333;
}
.section__title::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #f38181;
  margin: 40px auto 45px;
}

.section__text {
  max-width: 950px;
  margin: 0 auto 90px;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 1.6;
  color: #999;
}

.section__gallery {
  margin-bottom: 110px;
}

.section__gallery--works {
  margin-bottom: 0;
}

.section__list {
  margin: 0;
  padding: 0;
  padding-bottom: 120px;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.section__item {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 800px) {
  .section__item {
    flex-direction: column;
  }
}

.section__card {
  flex-basis: 33%;
  margin-bottom: 15px;
}

.section__image-tablet {
  position: absolute;
  bottom: -580px;
  left: 28.5%;
}
@media (max-width: 1025px) {
  .section__image-tablet {
    width: 300px;
    height: auto;
    bottom: -400px;
  }
}

.section__image-phone {
  position: absolute;
  bottom: -675px;
  left: 52%;
}
@media (max-width: 1025px) {
  .section__image-phone {
    width: 200px;
    height: auto;
    bottom: -475px;
  }
}

.section__clients {
  padding-top: 60px;
  padding-bottom: 80px;
}

.section__blog {
  margin-bottom: 80px;
}

/* Gallery */
.gallery__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.gallery__item {
  background-color: #95e1d3;
  position: relative;
}

.gallery__item:not(:last-child) {
  margin-right: 10px;
}

.gallery__item.gallery__item--works {
  margin: 0;
}

.gallery__card {
  background: linear-gradient(to bottom, #f38181, #fce38a);
  position: relative;
  transition: all 0.3s linear;
}
.gallery__card:hover:not(.gallery__card--not-moving) {
  transform: translate(-10px, -10px);
}
.gallery__card:hover .gallery__image {
  opacity: 0.1;
}
.gallery__card:hover .gallery__icon {
  opacity: 1;
}
.gallery__card:hover .gallery__text {
  opacity: 1;
}
.gallery__card:hover .gallery__social-icons {
  opacity: 1;
}

.gallery__image {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
  transition: opacity 0.3s linear;
}

.gallery__icon {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.3s linear;
}

.gallery__icon--works {
  top: 40%;
}

.gallery__text {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.6;
  text-transform: uppercase;
  color: #fff;
  position: absolute;
  top: 130px;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 0.3s linear;
}

.gallery__text--works {
  top: 58%;
  transform: translate3d(-50%, -50%, 0);
  text-align: center;
}

.gallery__title {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #fff;
}

.gallery__subtitle {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
  text-transform: none;
  font-size: 15px;
}

.gallery__social-icons {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  opacity: 0;
  transition: opacity 0.5s linear;
}

.gallery__designation {
  text-align: center;
  position: absolute;
  bottom: -60px;
  left: 50%;
  transform: translateX(-50%);
}

.gallery__name {
  display: block;
  min-width: 210px;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  line-height: 1.6;
  color: #333;
}

.gallery__profession {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
  font-size: 15px;
  color: #999;
}

/* Statistics */
.statistics {
  background-color: #95e1d3;
}

.statistics__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.statistics__item {
  text-align: center;
  flex-basis: 20%;
  padding: 80px 0;
  border-left: 1px solid #b5eae0;
}

.statistics__item:last-child {
  border-right: 1px solid #b5eae0;
}

.statistics__number {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 72px;
  text-transform: uppercase;
  color: #fff;
}

.statistics__text {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
}

/* Card */
.card {
  padding-left: 90px;
  padding-right: 35px;
  text-align: left;
  position: relative;
}

.card__image {
  position: absolute;
  top: 0;
  left: 33px;
}

.card__title {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #333;
  margin-bottom: 13px;
}

.card__text {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 1.6;
  color: #999;
}

/* Rule */
.rule {
  width: 100%;
  border-top: 1px solid #999;
  margin: 50px 0;
  padding: 0;
  opacity: 0.5;
}
@media (max-width: 800px) {
  .rule {
    display: none;
  }
}

/* What we do */
.what-we-do {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 120px;
}

.what-we-do__image {
  display: block;
  width: 100%;
  max-width: 570px;
  height: auto;
  object-fit: contain;
  margin: 0 15px 15px 15px;
}

.what-we-do__list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: left;
  min-width: 360px;
  max-width: 570px;
  flex-grow: 1;
}

.what-we-do__item {
  position: relative;
}

.what-we-do__title {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  color: #333;
  border: 1px solid #e5e5e5;
  padding: 20px 60px;
  position: relative;
}
.what-we-do__title--opened ~ .what-we-do__text {
  display: block;
}
.what-we-do__title--closed {
  cursor: pointer;
}
.what-we-do__title--closed ~ .what-we-do__text {
  display: none;
}
.what-we-do__title--closed + .what-we-do__button-toggle {
  transform: rotate(225deg);
  top: 15px;
  cursor: pointer;
}
.what-we-do__title::before {
  content: "";
  width: 30px;
  height: 30px;
  background-image: url("../img/wedo/photography.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 13px;
  left: 15px;
}
.what-we-do__title--creativity::before {
  background-image: url("../img/wedo/creativity.svg");
}
.what-we-do__title--design::before {
  background-image: url("../img/wedo/web-design.svg");
}

.what-we-do__text {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-style: italic;
  line-height: 1.7;
  color: #999;
  border: 1px solid #e5e5e5;
  border-top: none;
  padding: 20px 50px 25px 20px;
}

.what-we-do__button-toggle {
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: transparent;
  border: none;
  border-top: 2px solid #ccc;
  border-left: 2px solid #ccc;
  width: 20px;
  height: 20px;
  transform: rotate(45deg);
  position: absolute;
  top: 23px;
  right: 20px;
}

/* Reviews */
.reviews {
  background-color: #fff;
  padding: 80px 0;
}

.reivews--bg-gray {
  background-color: #f8f8f8;
}

/* Slider */
.slider {
  position: relative;
}

.slider__list {
  margin: 0;
  padding: 0 90px;
  list-style: none;
}

.slider__item {
  display: none;
  flex-direction: column;
  position: relative;
  padding-left: 200px;
}
.slider__item--current {
  display: flex;
}

.slider__image {
  position: absolute;
  top: 0;
  left: 0;
}

.slider__image--round {
  border-radius: 50%;
}

.slider__text {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 24px;
  line-height: 1.5;
  font-style: italic;
  color: #999;
}

.slider__autor {
  padding-left: 65px;
  font-family: "Kaushan Script", sans-serif;
  font-size: 24px;
  color: #333;
  position: relative;
}
.slider__autor::before {
  content: "";
  width: 60px;
  height: 3px;
  background-color: #f38181;
  position: absolute;
  top: 13px;
  left: 0;
}

.slider__button {
  margin: 0;
  padding: 0;
  width: 25px;
  height: 25px;
  background-color: transparent;
  border: none;
  border-top: 2px solid #ccc;
  border-left: 2px solid #ccc;
}

.slider__button:not(:disabled) {
  cursor: pointer;
}

.slider__button--prev {
  position: absolute;
  top: 65px;
  left: 35px;
  transform: rotate(-45deg);
}
.slider__button--prev:hover:not(:disabled) {
  border-top: 2px solid #999999;
  border-left: 2px solid #999999;
}

.slider__button--next {
  position: absolute;
  top: 65px;
  right: 35px;
  transform: rotate(135deg);
}
.slider__button--next:hover:not(:disabled) {
  border-top: 2px solid #999999;
  border-left: 2px solid #999999;
}

.slider__button:disabled {
  opacity: 0.3;
}

/* Social icons */
.social-icons__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.social-icons__item:not(:last-child) {
  border-right: 1px solid #f9bd87;
}

.social-icons__link {
  width: 56px;
  height: 56px;
  display: flex;
  background-color: #fce38a;
  transition: all 0.3s linear;
}
.social-icons__link:hover {
  background-color: #f38181;
  fill: #fff;
}
.social-icons__link:hover .social-icons__icon {
  fill: #fff;
}

.social-icons__icon {
  fill: #f38181;
  margin: auto;
}

/* Logos */
.logos {
  background-color: #f8f8f8;
}

.logos__list {
  margin: 0;
  padding: 50px 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logos__item:not(:last-child) {
  padding-right: 15px;
}

.logos__image {
  display: block;
  width: 100%;
  height: auto;
}

/* Clients */
.clients {
  text-align: left;
}

.clients__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 65px;
}

/* Card of client */
.card-client {
  text-align: left;
  position: relative;
  padding-left: 180px;
}

.card-client__image {
  display: block;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 40px;
}

.card-client__title {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #333;
}

.card-client__profession {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 300;
  font-size: 15px;
  color: #666666;
}
.card-client__profession:after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #f38181;
  margin: 20px 0;
}

.card-client__text {
  max-width: 380px;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 1.6;
  color: #999;
}

/* Blog */
.blog__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
}

.blog__image-card {
  position: relative;
}

.blog__image {
  display: block;
  width: 100%;
  height: auto;
  object-fit: contain;
  margin-bottom: 20px;
}

.blog__time {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
  font-size: 15px;
  color: #fff;
  width: 60px;
  height: 70px;
  background-color: #95e1d3;
  position: absolute;
  bottom: 10px;
  left: -10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.blog__time b {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 30px;
  color: #fff;
}

.blog__title {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  text-align: left;
  color: #333;
  margin-bottom: 10px;
}

.blog__text {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 15px;
  text-align: left;
  line-height: 1.6;
  color: #999;
}
.blog__text::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #e5e5e5;
  margin: 15px 0;
}

.blog__marks {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
  font-size: 15px;
  text-align: left;
  color: #999;
}

.blog__views {
  padding-left: 30px;
  position: relative;
}
.blog__views::before {
  content: "";
  width: 20px;
  height: 12px;
  background-image: url("../img/blog/views.svg");
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.blog__messages {
  padding-left: 30px;
  margin-left: 18px;
  position: relative;
}
.blog__messages::before {
  content: "";
  width: 20px;
  height: 15px;
  background-image: url("../img/blog/messages.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: absolute;
  top: 50%;
  left: 2px;
  transform: translateY(-50%);
}

/* Map */
.map {
  background-image: url("../img/map/map-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.map__link {
  margin: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  opacity: 0.5;
  color: #ea2424;
  transition: opacity 0.3s linear;
}
.map__link:hover {
  opacity: 1;
}

.map__image {
  margin-bottom: 20px;
  fill: #ea2424;
}

.map__text {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
}
.map__text::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #ea2424;
  margin: 20px auto 0;
}

/* Page footer */
.page-footer {
  background-color: #f8f8f8;
  padding: 75px 0;
}
@media (max-width: 1025px) {
  .page-footer {
    padding-bottom: 30px;
  }
}

.page-footer__wrapper::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #e5e5e5;
  margin-top: 70px;
  margin-bottom: 20px;
}

.page-footer__content {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1025px) {
  .page-footer__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
  }
}

.page-footer__copyright {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-align: center;
  color: #333;
}
.page-footer__copyright a {
  text-decoration: none;
  color: #f38181;
}
.page-footer__copyright a:visited {
  color: #f38181;
}
.page-footer__copyright a:hover {
  color: #ea2424;
}

/* About */
.about {
  max-width: 470px;
}
@media (max-width: 1025px) {
  .about {
    max-width: none;
  }
}

.about__logo {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 46px;
  color: #ccc;
  margin-bottom: 25px;
}

.about__text {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 1.6;
  color: #999;
  margin-bottom: 45px;
}

.about__followers {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: #333;
}
.about__followers b {
  font-weight: 700;
  font-size: 18px;
}
.about__followers::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #e5e5e5;
  margin: 15px 0;
}

.about__social {
  display: flex;
  margin-bottom: 35px;
}

.about__follow {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
  font-size: 15px;
  color: #999;
  margin-right: 20px;
}

.about__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.about__item:not(:last-child) {
  margin-right: 10px;
}

.about__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
}
.about__link:hover .about__icon {
  fill: #46cbb2;
  transform: scale(1.2);
  transition: all 0.2s linear;
}

.about__form {
  display: flex;
}

.about__form-input {
  padding: 15px 15px;
  margin: 0;
  min-width: 220px;
  font-family: "Roboto";
  font-weight: 300;
  font-style: italic;
  font-size: 15px;
  color: #666666;
  background-color: #fff;
  border: 1px solid #e7e7e7;
}
.about__form-input::placeholder {
  font-family: "Roboto";
  font-weight: 300;
  font-style: italic;
  font-size: 15px;
  color: #999;
}
.about__form-input:focus {
  outline: none;
  border: 1px solid #95e1d3;
}

.about__form-button {
  padding: 10px 30px;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  border: 1px solid #95e1d3;
  background-color: #95e1d3;
}
.about__form-button:focus {
  outline: none;
  border: 1px solid #46cbb2;
}
.about__form-button:hover {
  background-color: #46cbb2;
}
.about__form-button:active {
  background-color: #268573;
}

/* Blogs */
.blogs {
  max-width: 345px;
}

.blogs__title {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #333;
  margin-bottom: 40px;
}

.blogs__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.blogs__item {
  display: flex;
  align-items: center;
  margin-bottom: 42px;
}
.blogs__item:last-child {
  margin-bottom: 0;
}

.blogs__link {
  display: flex;
  text-decoration: none;
}

.blogs__image {
  display: block;
  width: 100%;
  height: auto;
  object-fit: contain;
}
.blogs__image:hover {
  opacity: 0.9;
  transition: opacity 0.2s linear;
}

.blogs__content {
  max-width: 200px;
  padding-left: 20px;
}

.blogs__text {
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  text-transform: uppercase;
  color: #333;
}
.blogs__text:hover {
  color: black;
  transition: color 0.2s linear;
}

.blogs__time {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
  font-size: 13px;
  line-height: 1.6;
  color: #999;
}

/* Instagram */
.instagram {
  max-width: 275px;
}

.instagram__title {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #333;
  margin-bottom: 40px;
}

.instagram__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3px;
  margin-bottom: 30px;
}

.instagram__link:hover {
  opacity: 0.9;
  transition: opacity 0.2s linear;
}

.instagram__image {
  display: block;
  width: 100%;
  height: auto;
  object-fit: contain;
}

.instagram__view-link {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: italic;
  font-size: 13px;
  text-decoration: none;
  color: #333;
}
.instagram__view-link:hover {
  color: black;
  transition: color 0.2s linear;
}